/* src/components/WeatherWidget.css */
.weather-widget {
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  margin: 0 auto; /* Centers the widget */
}

.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.search-input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
  width: 150px;
}

.search-button {
  padding: 10px 15px;
  background-color: #32cd32;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.search-button:hover {
  background-color: #28a745;
}

.weather-info {
  text-align: left;
}

.weather-info h3 {
  margin-bottom: 10px;
}

.error-message {
  color: red;
}
